import { ref } from 'vue';

const AVATAR_WORLDBOOK_NAME = 'è½¬ç”Ÿç³»ç»Ÿçš„å¤´åƒæ–‡ä»¶ï¼';
const MAX_IMAGE_SIZE = 500; // åŽ‹ç¼©åŽçš„æœ€å¤§å°ºå¯¸

/**
 * åŽ‹ç¼©å›¾ç‰‡å¹¶è½¬æ¢ä¸º base64
 */
async function compressImage(file: File): Promise<string> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = (e) => {
      const img = new Image();
      img.onload = () => {
        const canvas = document.createElement('canvas');
        let width = img.width;
        let height = img.height;

        // è®¡ç®—åŽ‹ç¼©åŽçš„å°ºå¯¸ï¼Œä¿æŒå®½é«˜æ¯”
        if (width > height && width > MAX_IMAGE_SIZE) {
          height = (height * MAX_IMAGE_SIZE) / width;
          width = MAX_IMAGE_SIZE;
        } else if (height >= width && height > MAX_IMAGE_SIZE) {
          width = (width * MAX_IMAGE_SIZE) / height;
          height = MAX_IMAGE_SIZE;
        }

        canvas.width = width;
        canvas.height = height;
        const ctx = canvas.getContext('2d');
        if (!ctx) {
          reject(new Error('æ— æ³•èŽ·å– canvas context'));
          return;
        }

        ctx.drawImage(img, 0, 0, width, height);
        
        // è½¬æ¢ä¸º base64ï¼Œä½¿ç”¨ JPEG æ ¼å¼åŽ‹ç¼©
        const base64 = canvas.toDataURL('image/jpeg', 0.8);
        resolve(base64);
      };
      img.onerror = () => reject(new Error('å›¾ç‰‡åŠ è½½å¤±è´¥'));
      img.src = e.target?.result as string;
    };
    reader.onerror = () => reject(new Error('æ–‡ä»¶è¯»å–å¤±è´¥'));
    reader.readAsDataURL(file);
  });
}

/**
 * èŽ·å–æˆ–åˆ›å»ºå¤´åƒä¸–ç•Œä¹¦
 */
async function ensureAvatarWorldbook(): Promise<void> {
  const worldbooks = getWorldbookNames();
  if (!worldbooks.includes(AVATAR_WORLDBOOK_NAME)) {
    await createWorldbook(AVATAR_WORLDBOOK_NAME, []);
    // ç»‘å®šåˆ°å½“å‰èŠå¤©
    await rebindChatWorldbook('current', AVATAR_WORLDBOOK_NAME);
  }
}

/**
 * ä¿å­˜å¤´åƒåˆ°èŠå¤©çŸ¥è¯†ä¹¦
 */
async function saveAvatar(characterName: string, base64: string): Promise<void> {
  await ensureAvatarWorldbook();
  
  const worldbook = await getWorldbook(AVATAR_WORLDBOOK_NAME);
  
  // æŸ¥æ‰¾æ˜¯å¦å·²å­˜åœ¨è¯¥è§’è‰²çš„å¤´åƒæ¡ç›®
  const existingEntryIndex = worldbook.findIndex(
    entry => entry.strategy.keys.includes(characterName)
  );
  
  const entryData = {
    name: `${characterName}çš„å¤´åƒ`,
    enabled: true,
    strategy: {
      type: 'selective' as const,
      keys: [characterName],
      keys_secondary: { logic: 'and_any' as const, keys: [] },
      scan_depth: 1,
    },
    position: {
      type: 'after_character_definition' as const,
      role: 'system' as const,
      depth: 0,
      order: 999,
    },
    content: JSON.stringify({
      è§’è‰²åç§°: characterName,
      å¤´åƒæ•°æ®: base64,
      æ›´æ–°æ—¶é—´: new Date().toISOString(),
    }),
    probability: 100,
    recursion: {
      prevent_incoming: true,
      prevent_outgoing: true,
      delay_until: null,
    },
    effect: {
      sticky: null,
      cooldown: null,
      delay: null,
    },
  };
  
  if (existingEntryIndex >= 0) {
    // æ›´æ–°çŽ°æœ‰æ¡ç›®
    worldbook[existingEntryIndex] = {
      ...worldbook[existingEntryIndex],
      ...entryData,
    };
    await replaceWorldbook(AVATAR_WORLDBOOK_NAME, worldbook);
  } else {
    // åˆ›å»ºæ–°æ¡ç›®
    await createWorldbookEntries(AVATAR_WORLDBOOK_NAME, [entryData]);
  }
}

/**
 * è¯»å–è§’è‰²å¤´åƒ
 */
async function loadAvatar(characterName: string): Promise<string | null> {
  try {
    const worldbooks = getWorldbookNames();
    if (!worldbooks.includes(AVATAR_WORLDBOOK_NAME)) {
      return null;
    }
    
    const worldbook = await getWorldbook(AVATAR_WORLDBOOK_NAME);
    const entry = worldbook.find(
      e => e.strategy.keys.includes(characterName)
    );
    
    if (!entry) {
      return null;
    }
    
    const data = JSON.parse(entry.content);
    return data.å¤´åƒæ•°æ® || null;
  } catch (err) {
    console.error('[å¤´åƒç®¡ç†] è¯»å–å¤´åƒå¤±è´¥', err);
    return null;
  }
}

export function useAvatarManager() {
  const uploading = ref(false);
  const avatarUrl = ref<string | null>(null);
  
  /**
   * ä¸Šä¼ å¤´åƒ
   */
  const uploadAvatar = async (characterName: string): Promise<void> => {
    if (!characterName || characterName === 'è§’è‰²åç§°') {
      toastr?.warning?.('è¯·å…ˆè®¾ç½®è§’è‰²åç§°', 'å¤´åƒä¸Šä¼ ');
      return;
    }
    
    // åˆ›å»ºæ–‡ä»¶é€‰æ‹©å™¨
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = 'image/*';
    
    input.onchange = async (e) => {
      const file = (e.target as HTMLInputElement).files?.[0];
      if (!file) {
        return;
      }
      
      // éªŒè¯æ–‡ä»¶ç±»åž‹
      if (!file.type.startsWith('image/')) {
        toastr?.error?.('è¯·é€‰æ‹©å›¾ç‰‡æ–‡ä»¶', 'å¤´åƒä¸Šä¼ ');
        return;
      }
      
      // éªŒè¯æ–‡ä»¶å¤§å°ï¼ˆæœ€å¤§ 5MBï¼‰
      if (file.size > 5 * 1024 * 1024) {
        toastr?.error?.('å›¾ç‰‡æ–‡ä»¶è¿‡å¤§ï¼Œè¯·é€‰æ‹©å°äºŽ 5MB çš„å›¾ç‰‡', 'å¤´åƒä¸Šä¼ ');
        return;
      }
      
      uploading.value = true;
      
      try {
        // åŽ‹ç¼©å¹¶è½¬æ¢ä¸º base64
        const base64 = await compressImage(file);
        
        // ä¿å­˜åˆ°èŠå¤©çŸ¥è¯†ä¹¦
        await saveAvatar(characterName, base64);
        
        // æ›´æ–°æ˜¾ç¤º
        avatarUrl.value = base64;
        
        toastr?.success?.('å¤´åƒä¸Šä¼ æˆåŠŸ', 'å¤´åƒä¸Šä¼ ');
      } catch (err) {
        console.error('[å¤´åƒä¸Šä¼ ] å¤±è´¥', err);
        toastr?.error?.('å¤´åƒä¸Šä¼ å¤±è´¥ï¼š' + (err as Error).message, 'å¤´åƒä¸Šä¼ ');
      } finally {
        uploading.value = false;
      }
    };
    
    input.click();
  };
  
  /**
   * åŠ è½½å¤´åƒ
   */
  const loadAvatarForCharacter = async (characterName: string): Promise<void> => {
    if (!characterName || characterName === 'è§’è‰²åç§°') {
      avatarUrl.value = null;
      return;
    }
    
    try {
      const url = await loadAvatar(characterName);
      avatarUrl.value = url;
    } catch (err) {
      console.error('[å¤´åƒåŠ è½½] å¤±è´¥', err);
      avatarUrl.value = null;
    }
  };
  
  return {
    uploading,
    avatarUrl,
    uploadAvatar,
    loadAvatarForCharacter,
  };
}